<template>
  <div id="app">
    <translateform v-on:translateTotxt="translateTxt"></translateform>
    <translateOut v-text="translated"></translateOut>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOut from './components/TranslateOut'

export default {
  name: 'App',
  data(){
    return {
      translated:""
    }
  },
  methods:{
    translateTxt:function(text,language){
          this.$http.get("https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180324T082528Z.6d8d4f5020e4cb4f.90039c13fb5a96e5dce9755235743d0de2834113&lang="+language+"&text="+text)
           .then((response)=>{
            this.translated = response.body.text[0]
       })
    }
  },
  components: {
    "translateform":TranslateForm,
    "translateOut":TranslateOut
  },
  created(){
   
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



