<template>
  <div id="translateform" class="row">
    <h1>{{title}}</h1>
    <h5 class="text-muted">高效 / 方便 / 专业</h5>
    <div class="col-md-6 col-md-offset-3">
      <form v-on:submit="translate" class="well form-inline" id="formbox">
        <input type="text" v-model="translateTotxt" class="form-control">
        <select v-model="translateTolang" class="form-control">
          <option value="en">English</option>
          <option value="zh">Chinese</option>
          <option value="de">German</option>
          <option value="fr">French</option>
          <option value="ko">Korean</option>          
          <option value="ja">Japanese</option>
          <option value="ru">Russian</option>         
        </select>
        <input type="submit" value="翻译" class="btn btn-primary btn-sm ">
      </form> 
    </div>
  </div>
</template>

<script>
export default {
  name: 'translateform',
  data () {
    return {
      title:"在线翻译",
      translateTotxt:"",
      translateTolang:""
    }
  },
  methods:{
    translate:function(e){
      this.$emit("translateTotxt",this.translateTotxt,this.translateTolang);
      e.preventDefault();
    }
  },
  created(){
    this.translateTolang="en"
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#formbox{
  border: 1px solid #ccc;
  border-radius: 2px;
}
.btn{
  margin: 10px;
}
</style>



